 
.contact-container-CDN {
 padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    border-radius: 4px;
    border: 2px solid #00000059;
    font-family: 'Arial', sans-serif;
    color: #333;
    margin-top: 6%;
}
 
.contact-header-CDN {
  text-align: center;
  margin-bottom: 30px;
}

.contact-header-CDN h1 {
  font-size: 2rem;
  color: #007BFF;  
}

.contact-header-CDN p {
  font-size: 1rem;
  color: #555;
}
 
.contact-form-section-CDN {
  margin-bottom: 30px;
}

 
.contact-form-CDN {
  display: flex;
  flex-direction: column;
}

.contact-form-CDN label {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #007BFF;  
}

.contact-form-CDN input, .contact-form-CDN textarea {
  margin-bottom: 20px;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #CDNc;
  border-radius: 5px;
}

.contact-form-CDN button {
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #007BFF;  
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact-form-CDN button:hover {
  background-color: #0056b3;  
}