/* Navbar styles */
.navbar {
    display: flex;
    flex-wrap: wrap; /* Allow wrapping on smaller screens */
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
    font-size: 17px;
    box-sizing: border-box; /* Include padding and border in the width */
}

/* Ensure responsiveness of the search bar */
.search {
    flex: 1;
    min-width: 200px; /* Minimum width for smaller screens */
}

/* Adjust cart and user button containers */
.cart, .user {
    display: flex;
    align-items: center;
    margin-left: 10px; /* Adjust spacing */
}

/* Media Queries for Mobile Devices */
@media (max-width: 768px) {
    .navbar {
        padding: 10px; /* Adjust padding for smaller screens */
        font-size: 16px; /* Adjust font size */
    }

    .search {
        flex: 1 1 100%; /* Allow search bar to take full width */
        margin-bottom: 10px; /* Add space below search bar */
    }

    .cart, .user {
        margin-left: 0; /* Remove margin on smaller screens */
    }
}

