/* Container for the video and content */
.video-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

/* Background video styles */
.bg-video-HC {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
}

/* Basic styles for the help container */
.help-container-HC {
  top:15%;
  position: relative;
  z-index: 1;
  padding: 20px;
  max-width: 800px;
  border:2px solid #a68f93a3;
  border-radius: 2px;
  margin: 0 auto;
  font-family: 'Arial', sans-serif;
  color: #010202;
}

/* Header styles */
.help-header-HC {
  text-align: center;
  margin-bottom: 30px;
}

.help-header-HC h1 {
  font-size: 2rem;
}

.help-header-HC p {
  font-size: 1rem;
}

/* FAQ section styles */
.faq-section-HC {
  margin-bottom: 30px;
}

.faq-section-HC h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.faq-item-HC {
  margin-bottom: 20px;
}

.faq-item-HC h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.faq-item-HC p {
  font-size: 1rem;
}

/* Contact button styles */
.contact-btn-HC {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  color: #fff;
  background-color: #007BFF; /* Blue color */
  border: none;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact-btn-HC:hover {
  background-color: #0056b3; /* Darker blue */
}